  font-family: var(--font-family);
  font-weight: 500;
  font-size: 20px;
  line-height: 140%;
  color: var(--belyy);
}
.desc--dark {
  color: var(--chernyy);
}
.desc--blue {
  color: var(--svetlo-siniy);
}

.text {
